.MainNavBar {
    position: absolute;
    width: 100%;
    height: 100px;
    left: 0;
    top: 0;
    background-color: rgb(50, 54, 57);    
}
.MNB_menu__el {
    color: #599999;
    font-size: 16px; 
    font-family: 'Lucida Sans' serif;
    font-weight: 700;
    max-width: 100px;
    max-height: 90px;  
    align-self: center;
}
.MNB_menu {  
    width: 490px;
    height: 100%;
    position: absolute;
    left: 60%;
    top: 0px;
    display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: space-between;	
    margin: auto;    
}
.MNB_menu__el:hover {
    color: #FFF;
}